.footer {
  list-style: none;
  background-color: var(--green);
  display: flex;
  flex-direction: column;
  background-color: var(--green);
  color: var(--blue);
  list-style: none;
  font-family: "Poppins";
}

.column {
  margin: 20px 59px auto 146px;
  align-items: center;
  width: 208px;
}

.store-title {
  margin-top: 56px;
  margin-bottom: 12px;
}

.list-title {
  margin-top: 0px auto 20px auto;
}

.list {
  list-style-type: none;
  color: var(--blue);
}

.link-footer {
  color: var(--blue);
  text-decoration: none;
}

.text {
  width: 208px;
  text-align: center;
}

.arrow {
  color: var(--blue);
  margin: 16px 16px 16px 361px;
}

.social-media {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.icons {
  margin-right: 11px;
  color: var(--blue);
}
