.header {
  background-image: url("../images/header.jpg");
  align-items: center;
  background-size: cover;
  background-position: center;
  width: 100vw;
  font-family: "Poppins";
}

.head {
  width: 100vw;
  list-style: none;
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.bars {
  display: flex;
  justify-content: flex-start;
  margin: 55px 43px 136px 33px;
  width: 24x;
  height: 24px;
}

.logo {
  margin: 19px 101px 100px 0px;
  width: 213px;
  height: 96px;
}

.main {
  align-items: center;
  display: flex;
  flex-direction: column;
}

/*END OF HEADER MENU AND LOGO*/

.title {
  display: flex;
  justify-content: flex-start;
  padding-top: 215px;
  padding-bottom: 0px;
  color: var(--green);
  font-size: 24px;
}

.button {
  margin: 12px 119px 64px 119px;
  width: 176px;
  height: 48px;
  font-size: 16px;
  color: white;
  background-color: var(--green);
  font-family: "Poppins";
  text-transform: capitalize;
}

.button:hover {
  transform: scale(1.2);
  transition-duration: 0.65s;
}

@media all and (min-width: 900px) {
  .bars {
    margin: 75px 123.5px 0px 82px;
  }

  .logo {
    margin: 39px 334px 0px 123.5px;
  }

  .main {
    display: flex;
  }

  .title {
    margin: 0px;
    width: 430px;
    font-size: 36px;
  }

  .button {
    margin: 0px 0px 0px 0px;
    align-items: flex-start;
  }
}
